.sign_in {
    .split {
        width: 80%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-items: center;
        align-content: center;
        justify-items: center;
        row-gap: 1.5rem;

        .goo {
            padding: 1rem 2rem;
            font-size: 1.2rem;
            cursor: pointer;
        }

        .spin {
            width: 100%;
            position: relative;
            justify-items: center;
            justify-content: center;
            text-align: center;

            .or {
                width: 5rem;
                padding: .2rem .9rem;
               background-color: #fff;
            }

            .line {
                position: absolute;
                top: .6rem;
                width: 100%;
                height: .1px;
                background-color: #999;
                z-index: -10;
            }
        }

    }

    .form {
        .input {
            border: none;
            border-bottom: 1px solid #000;
            outline: none;
        }
        .log {
            width: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-items: center;
            column-gap: 1.5rem;
        }
        .other {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-items: center;
            row-gap: 1.5rem;

            .text {
                font-size: 1rem;
                text-align: center;
            }
        }
    }

}


@media screen and (min-width : 680px) {
    .sign_in {
        .form {
            .log {
                flex-direction: row;
            }
        }
    }
}